*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font: normal 14px/1.33 "Museo Sans Cyrl", sans-serif;
  color: #0d0d0d;
}

.container {
  max-width: 950px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (max-width: 991px) {
  .container {
    max-width: 720px;
  }
}

@media (max-width: 767px) {
  max-width: 540px;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

.promo {
  min-height: 650px;
  padding: 21px 0 93px 0;
  background: url("../images/main.jpg") center center/cover no-repeat;
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 91px;
}

.promo__title {
  font-weight: bold;
  line-height: 1;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
}

@media (max-width: 991px) {
  .promo__title {
    font-size: calc(38px + 6.8 * ((100vw - 320px) / 1366));
  }
}

@media (min-width: 991px) {
  .promo__title {
    font-size: calc(38px + 4 * (100vw / 1366));
  }
}

.promo__subtitle {
  font-weight: normal;
  line-height: 1;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  margin-top: 17px;
}

@media (max-width: 991px) {
  .promo__subtitle {
    font-size: calc(22px + 5.1 * ((100vw - 320px) / 1366));
  }
}

@media (min-width: 991px) {
  .promo__subtitle {
    font-size: calc(22px + 3 * (100vw / 1366));
  }
}

.promo__icons {
  display: flex;
  margin: 66px 0;
}

.promo__icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 35px;
}

.promo__icon img {
  display: block;
  width: 45px;
  height: 46px;
}

.promo__icon span {
  text-align: center;
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
}

.promo__text {
  color: #ffffff;
  font: normal 16px/1 "Museo Sans Cyrl", sans-serif;
  margin-top: 18px;
}

.promo__link {
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: underline;
  transition: .3s all;
}

.promo__link:hover {
  color: #e6e6e6;
}

@media (max-width: 991px) {
  .promo {
    height: 100vh;
    background-position-x: 20%;
    padding-bottom: 0;
  }
  .promo__wrapper {
    align-items: center;
  }
}

@media (max-width: 575px) {
  .promo__wrapper {
    margin-top: 30px;
  }
  .promo__subtitle {
    line-height: 1.3;
  }
  .promo__icons {
    justify-content: center;
    flex-wrap: wrap;
    margin: 0;
    margin-top: 20px;
  }
  .promo__icon {
    margin-top: 10px;
  }
  .promo__icon:last-child {
    margin-bottom: 20px;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  display: block;
  width: 250px;
}

.header__logo img {
  width: 100%;
}

.header__official {
  text-align: center;
  color: #ffffff;
  font-size: 12px;
  font-weight: 500;
}

.header__official span {
  display: block;
  text-align: center;
  font-size: 18px;
}

.header__contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.header__phone {
  display: block;
  color: #ffffff;
  font-weight: 700;
  transition: .3s all;
}

@media (max-width: 991px) {
  .header__phone {
    font-size: calc(15px + 5.1 * ((100vw - 320px) / 1366));
  }
}

@media (min-width: 991px) {
  .header__phone {
    font-size: calc(15px + 3 * (100vw / 1366));
  }
}

.header__phone:hover {
  color: #e6e6e6;
  text-decoration: none;
}

@media (max-width: 767px) {
  .header__logo {
    display: block;
    margin-right: 5px;
  }
  .header__official {
    display: none;
  }
  .header__contacts {
    align-items: center;
    margin-left: 5px;
  }
}

.button {
  width: 150px;
  height: 30px;
  border-radius: 5px;
  background-color: #c70101;
  border: none;
  outline: none;
  cursor: pointer;
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
  transition: .3s all;
}

@media (max-width: 991px) {
  .button {
    font-size: calc(10px + 3.4 * ((100vw - 320px) / 1366));
  }
}

@media (min-width: 991px) {
  .button {
    font-size: calc(10px + 2 * (100vw / 1366));
  }
}

.button:hover {
  background-color: #e90202;
  color: #f0ebc4;
}

.button--main {
  width: 300px;
  height: 50px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
}

.title {
  color: #0d0d0d;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}

@media (max-width: 991px) {
  .title {
    font-size: calc(25px + 8.5 * ((100vw - 320px) / 1366));
  }
}

@media (min-width: 991px) {
  .title {
    font-size: calc(25px + 5 * (100vw / 1366));
  }
}

.title-header {
  color: #0d0d0d;
  font-weight: bold;
}

@media (max-width: 991px) {
  .title-header {
    font-size: calc(18px + 0 * ((100vw - 320px) / 1366));
  }
}

@media (min-width: 991px) {
  .title-header {
    font-size: calc(18px + 0 * (100vw / 1366));
  }
}

.title-header--center {
  text-align: center;
  margin-top: 38px;
}

.title-footer {
  color: #0d0d0d;
  font-weight: regular;
}

@media (max-width: 991px) {
  .title-footer {
    font-size: calc(14px + 0 * ((100vw - 320px) / 1366));
  }
}

@media (min-width: 991px) {
  .title-footer {
    font-size: calc(14px + 0 * (100vw / 1366));
  }
}

.title-footer--center {
  text-align: center;
  margin-top: 25px;
}

@media (max-width: 767px) {
  .title-header--center {
    margin-top: 20px;
  }
}

.benefits {
  background-color: #f6f6f6;
  padding-top: 70px;
  padding-bottom: 78px;
}

.benefits__inner {
  display: flex;
  justify-content: center;
  flex-basis: 922px;
  flex-wrap: wrap;
  margin-top: 65px;
}

.benefits__item {
  width: 33.333333%;
}

.benefits__item img {
  display: block;
  margin: 0 auto;
}

@media (max-width: 991px) {
  .benefits__item {
    width: 40%;
  }
  .benefits__item:nth-child(2) {
    margin: 0 54px;
  }
  .benefits__item:nth-child(3) {
    margin-top: 20px;
  }
}

@media (max-width: 767px) {
  .benefits {
    padding-top: 50px;
  }
  .benefits__inner {
    margin-top: 50px;
  }
  .benefits__item {
    width: 43%;
  }
  .benefits__item:nth-child(2) {
    margin-top: 20px;
  }
}

@media (max-width: 575px) {
  .benefits__item {
    width: 75%;
  }
}
