.promo {
    min-height: 650px;
    padding: 21px 0 93px 0;
    background: url('../images/main.jpg') center (center/cover) no-repeat;
    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-top: 91px; }
    &__title {
        @include res-font(42, 38);
        font-weight: bold;
        line-height: 1;
        color: #ffffff;
        text-transform: uppercase;
        text-align: center; }
    &__subtitle {
        @include res-font(25, 22);
        font-weight: normal;
        line-height: 1;
        color: $color-white;
        text-transform: uppercase;
        text-align: center;
        margin-top: 17px; }
    &__icons {
        display: flex;
        margin: 66px 0; }
    &__icon {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 35px;
        img {
            display: block;
            width: 45px;
            height: 46px; }
        span {
            text-align: center;
            color: $color-white;
            font-size: 14px;
            font-weight: 500; } }
    &__text {
        color: $color-white;
        font: normal 16px/1 $main-font;
        margin-top: 18px; }
    &__link {
        color: $color-white;
        text-transform: uppercase;
        text-decoration: underline;
        transition: .3s all;
        &:hover {
            color: #e6e6e6; } } }

@media (max-width: 991px) {
    .promo {
        height: 100vh;
        background-position-x: 20%;
        padding-bottom: 0;
        &__wrapper {
            align-items: center; } } }

@media (max-width: 575px) {
    .promo {
        &__wrapper {
            margin-top: 30px; }
        &__subtitle {
            line-height: 1.3; }
        &__icons {
            justify-content: center;
            flex-wrap: wrap;
            margin: 0;
            margin-top: 20px; }
        &__icon {
            margin-top: 10px; }
        &__icon:last-child {
            margin-bottom: 20px; } } }
