.benefits {
    background-color: #f6f6f6;
    padding-top: 70px;
    padding-bottom: 78px;
    &__inner {
        display: flex;
        justify-content: center;
        flex-basis: 922px;
        flex-wrap: wrap;
        margin-top: 65px; }
    &__item {
        width: 33.333333%;
        img {
            display: block;
            margin: 0 auto; } } }

@media (max-width: 991px) {
    .benefits {
        &__item {
            width: 40%; }
        &__item:nth-child(2) {
            margin: 0 54px; }
        &__item:nth-child(3) {
            margin-top: 20px; } } }

@media (max-width: 767px) {
    .benefits {
        padding-top: 50px;
        &__inner {
            margin-top: 50px; }
        &__item {
            width: 43%; }
        &__item:nth-child(2) {
            margin-top: 20px; } } }

@media (max-width: 575px) {
    .benefits {
        &__item {
            width: 75%; } } }
