.header {
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &__logo {
        display: block;
        width: 250px;
        img {
            width: 100%; } }
    &__official {
        text-align: center;
        color: $color-white;
        font-size: 12px;
        font-weight: 500;
        span {
            display: block;
            text-align: center;
            font-size: 18px; } }
    &__contacts {
        display: flex;
        flex-direction: column;
        align-items: flex-end; }

    &__phone {
        display: block;
        color: $color-white;
        @include res-font(18, 15);
        font-weight: 700;
        transition: .3s all;
        &:hover {
            color: #e6e6e6;
            text-decoration: none; } } }

@media (max-width: 991px) {
    .header {
        &__wrapper {} } }

@media (max-width: 767px) {
    .header {
        &__logo {
            display: block;
            margin-right: 5px; }
        &__official {
            display: none; }
        &__contacts {
            align-items: center;
            margin-left: 5px; } } }
