.title {
    color: $color-titles;
    @include res-font(30, 25);
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    &-header {
        color: $color-titles;
        @include res-font(18, 18);
        font-weight: bold;
        &--center {
            text-align: center;
            margin-top: 38px; } }
    &-footer {
        color: $color-titles;
        @include res-font(14, 14);
        font-weight: regular;
        &--center {
            text-align: center;
            margin-top: 25px; } } }

@media (max-width: 767px) {
    .title {
        &-header {
            &--center {
                margin-top: 20px; } } } }
