$main-font: 'Museo Sans Cyrl', sans-serif;

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0; }

body {
    font: normal 14px/1.33 $main-font;
    color: #0d0d0d; }

.container {
    max-width: 950px;
    margin: 0 auto;
    padding: 0 15px; }

@media (max-width: 991px) {
    .container {
        max-width: 720px; } }

@media (max-width: 767px) {
    max-width: 540px; }


h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0; }
